body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.typing-statistics {
  margin: 0 auto;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  align-items: center;
}

.typing-statistics__secondary {
  display: flex;
  position: relative;
}

.typing-statistics__secondary::after {
  transition: all 0.18s ease-out 0s;
  opacity: 0;
}

.typing-statistics__secondary:hover::after {
  opacity: 1;
  position: absolute;
  top: -20px;
  content: "To'g'ri / Noto'g'ri / Qolib ketgan / Umumiy";
  background: var(--gradients-gradient-blue, linear-gradient(315deg, #E33D0E 0%, #FF7D58 100%));
  width: 310px;
  height: 30px;
  padding: 5px 10px;
  border-radius: 6px;
  left: -90px;
}
